/**
 * í”„ë¡œì íŠ¸ ëª©ë¡ ì»´í¬ë„ŒíŠ¸
 */
@Component({
  selector: 'app-project-list',
  standalone: true,
  imports: [
    CommonModule,
    MatCardModule,
    MatButtonModule,
    MatIconModule,
    MatChipsModule,
    MatProgressSpinnerModule
  ],
  templateUrl: './project-list.html',
  styleUrl: './project-list.scss'
})
export class ProjectList implements OnInit, OnDestroy {
  projectVM = inject(ProjectViewModelAbstract);
  
  ProjectStatusEnum = ProjectStatusEnum;
  
  ngOnInit(): void {
    // ViewModelì—ì„œ í”„ë¡œì íŠ¸ ë¡œë“œ
    this.projectVM.loadProjects();
  }

  ngOnDestroy(): void {
    // Cleanupì€ ViewModelì—ì„œ ì²˜ë¦¬
  }

  /**
   * ìƒíƒœ ë¼ë²¨ ê°€ì ¸ì˜¤ê¸°
   */
  getStatusLabel(status: ProjectStatusEnum): string {
    const labels = {
      [ProjectStatusEnum.Receive]: 'ì ‘ìˆ˜ë¨',
      [ProjectStatusEnum.Progress]: 'ì§„í–‰ì¤‘',
      [ProjectStatusEnum.Complete]: 'ì™„ë£Œë¨'
    };
    return labels[status] || status;
  }

  /**
   * ìƒíƒœ ìƒ‰ìƒ ê°€ì ¸ì˜¤ê¸°
   */
  getStatusColor(status: ProjectStatusEnum): string {
    const colors = {
      [ProjectStatusEnum.Receive]: 'accent',
      [ProjectStatusEnum.Progress]: 'primary',
      [ProjectStatusEnum.Complete]: 'warn'
    };
    return colors[status] || 'primary';
  }

  /**
   * ìƒíƒœ ì•„ì´ì½˜ ê°€ì ¸ì˜¤ê¸°
   */
  getStatusIcon(status: ProjectStatusEnum): string {
    const icons = {
      [ProjectStatusEnum.Receive]: 'inbox',
      [ProjectStatusEnum.Progress]: 'trending_up',
      [ProjectStatusEnum.Complete]: 'check_circle'
    };
    return icons[status] || 'folder';
  }

  /**
   * ìƒíƒœ ì•„ì´ì½˜ ìƒ‰ìƒ
   */
  getStatusIconColor(status: ProjectStatusEnum): string {
    const colors = {
      [ProjectStatusEnum.Receive]: '#ff9800',
      [ProjectStatusEnum.Progress]: '#2196f3',
      [ProjectStatusEnum.Complete]: '#4caf50'
    };
    return colors[status] || '#757575';
  }

  /**
   * í”„ë¡œì íŠ¸ ê·¸ë¼ë°ì´ì…˜ ë°°ê²½
   */
  getProjectGradient(status: ProjectStatusEnum): string {
    const gradients = {
      [ProjectStatusEnum.Receive]: 'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)',
      [ProjectStatusEnum.Progress]: 'linear-gradient(135deg, #4facfe 0%, #00f2fe 100%)',
      [ProjectStatusEnum.Complete]: 'linear-gradient(135deg, #43e97b 0%, #38f9d7 100%)'
    };
    return gradients[status] || 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)';
  }

  /**
   * í”„ë¡œì íŠ¸ ê¸°ê°„ ê³„ì‚° (ì¼)
   */
  getDuration(project: IProject): number {
    const start = new Date(project.startDate);
    const end = new Date(project.endDate);
    const diff = end.getTime() - start.getTime();
    return Math.ceil(diff / (1000 * 60 * 60 * 24));
  }

  /**
   * ì§„í–‰ë¥  ê³„ì‚°
   */
  getProgress(project: IProject): number {
    const now = new Date();
    const start = new Date(project.startDate);
    const end = new Date(project.endDate);
    
    if (now < start) return 0;
    if (now > end) return 100;
    
    const total = end.getTime() - start.getTime();
    const current = now.getTime() - start.getTime();
    return Math.round((current / total) * 100);
  }

  /**
   * í”„ë¡œì íŠ¸ ìˆ˜ì •
   */
  onEdit(project: IProject): void {
    this.projectVM.openEditDialog(project);
  }

  /**
   * í”„ë¡œì íŠ¸ ì‚­ì œ
   */
  onDelete(project: IProject): void {
    this.projectVM.deleteProject(project.id);
  }

  /**
   * ìƒˆ í”„ë¡œì íŠ¸ ìƒì„±
   */
  onCreate(): void {
    this.projectVM.openCreateDialog();
  }
}
