import { Injectable, inject } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable, of, catchError } from 'rxjs';
import { map } from 'rxjs/operators';
import { IProject, ProjectStatusEnum } from '../interfaces/project.interface';
import { environment } from '../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ProjectService {
  private readonly http = inject(HttpClient);
  private readonly baseUrl = environment.baseSyncUrl + '/project';

  /**
   * ì‚¬ìš©ì IDë¡œ í”„ë¡œì íŠ¸ ëª©ë¡ ì¡°íšŒ
   * SyncBI-Station API: GET /project?userId={id}
   */
  getProjectsByUserId(userId: string): Observable<IProject[]> {
    const params = new HttpParams().set('userId', userId);
    
    return this.http.get<any[]>(this.baseUrl, { params }).pipe(
      map(projects => {
        
        // ë‚ ì§œ ì—­ì§ë ¬í™”
        return projects.map(project => ({
          ...project,
          startDate: new Date(project.startDate),
          endDate: new Date(project.endDate)
        }));
      }),
      catchError(error => {
        return of([]);
      })
    );
  }

  /**
   * ì¡°ì§ IDë¡œ í”„ë¡œì íŠ¸ ëª©ë¡ ì¡°íšŒ
   * SyncBI-Station API: GET /project?organizationId={id}
   */
  getProjectsByOrganizationId(organizationId: string): Observable<IProject[]> {
    const params = new HttpParams().set('organizationId', organizationId);
    
    return this.http.get<any[]>(this.baseUrl, { params }).pipe(
      map(projects => projects.map(project => ({
        ...project,
        startDate: new Date(project.startDate),
        endDate: new Date(project.endDate)
      }))),
      catchError(error => {
        return of([]);
      })
    );
  }

  /**
   * ìƒˆ í”„ë¡œì íŠ¸ ìƒì„±
   * SyncBI-Station API: POST /project
   */
  createProject(payload: {
    organizationId: string;
    name: string;
    startDate: Date;
    endDate: Date;
    leader: string;
    members: string[];
    description: string;
  }): Observable<any> {
    
    return this.http.post(this.baseUrl, payload).pipe(
      map((result: any) => {
        
        if (result.project) {
          result.project.startDate = new Date(result.project.startDate);
          result.project.endDate = new Date(result.project.endDate);
        }
        
        return result;
      }),
      catchError(error => {
        throw error;
      })
    );
  }

  /**
   * í”„ë¡œì íŠ¸ ì—…ë°ì´íŠ¸
   * SyncBI-Station API: PATCH /project
   */
  updateProject(payload: Partial<IProject> & { id: string }): Observable<IProject> {
    
    return this.http.patch<IProject>(this.baseUrl, payload).pipe(
      map(project => {
        
        return {
          ...project,
          startDate: new Date(project.startDate),
          endDate: new Date(project.endDate)
        };
      }),
      catchError(error => {
        throw error;
      })
    );
  }

  /**
   * í”„ë¡œì íŠ¸ ì‚­ì œ
   * SyncBI-Station API: DELETE /project?projectIdx={id}
   */
  deleteProject(projectId: string): Observable<void> {
    const params = new HttpParams().set('projectIdx', projectId);
    
    return this.http.delete<void>(this.baseUrl, { params }).pipe(
      map(() => {
      }),
      catchError(error => {
        throw error;
      })
    );
  }
}
