import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ProjectLayout } from './components/project-layout/project-layout';
import { ProjectList } from './components/project-list/project-list';
import { Kanban } from './components/kanban/kanban';
import { GanttComponent } from './components/gantt/gantt';
import { ProjectAuthGuard } from './guards/auth.guard';

const routes: Routes = [
  {
    path: 'auth',
    loadChildren: () => import('./auth/auth-routing.module').then(m => m.ProjectAuthRoutingModule)
  },
  {
    path: '',
    component: ProjectLayout,
    canActivate: [ProjectAuthGuard],
    canActivateChild: [ProjectAuthGuard],
    children: [
      {
        path: '',
        redirectTo: 'list',
        pathMatch: 'full'
      },
      {
        path: 'list',
        component: ProjectList
      },
      {
        path: 'kanban',
        component: Kanban
      },
      {
        path: 'gantt',
        component: GanttComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProjectRoutingModule { }
