import { Component, OnInit, OnDestroy } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatCardModule } from '@angular/material/card';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatChipsModule } from '@angular/material/chips';
import { Subject, takeUntil } from 'rxjs';
import { ProjectViewModelAbstract } from '../../abstracts/project-viewmodel.abstract';
import { ProjectViewModel } from '../../viewmodels/project.viewmodel';
import { ScheduleViewModelAbstract } from '../../abstracts/schedule-viewmodel.abstract';
import { ScheduleViewModel } from '../../viewmodels/schedule.viewmodel';
import { ProjectService } from '../../services/project.service';
import { ScheduleService } from '../../services/schedule.service';
import { IProject, ProjectStatusEnum } from '../../interfaces/project.interface';
import { ISchedule } from '../../interfaces/schedule.interface';

interface GanttTask {
  id: string;
  name: string;
  startDate: Date;
  endDate: Date;
  progress: number;
  status: ProjectStatusEnum;
  schedules: ISchedule[];
  color: string;
}

interface TimelineColumn {
  date: Date;
  label: string;
  isToday: boolean;
}

@Component({
  selector: 'app-gantt',
  standalone: true,
  imports: [
    CommonModule,
    MatCardModule,
    MatButtonModule,
    MatIconModule,
    MatProgressSpinnerModule,
    MatTooltipModule,
    MatChipsModule
  ],
  providers: [
    { provide: ProjectViewModelAbstract, useClass: ProjectViewModel },
    { provide: ScheduleViewModelAbstract, useClass: ScheduleViewModel },
    ProjectService,
    ScheduleService
  ],
  templateUrl: './gantt.html',
  styleUrls: ['./gantt.scss']
})
export class GanttComponent implements OnInit, OnDestroy {
  private destroy$ = new Subject<void>();
  
  projects: IProject[] = [];
  ganttTasks: GanttTask[] = [];
  timelineColumns: TimelineColumn[] = [];
  loading = false;
  error: string | null = null;

  // Gantt config
  dayWidth = 40; // ê° ë‚ ì§œ ì…€ì˜ ë„ˆë¹„ (px)
  rowHeight = 60; // ê° í–‰ì˜ ë†’ì´ (px)
  viewMode: 'day' | 'week' | 'month' = 'week';
  
  // Timeline range
  startDate: Date = new Date();
  endDate: Date = new Date();
  today: Date = new Date();

  constructor(
    private projectViewModel: ProjectViewModelAbstract,
    private scheduleViewModel: ScheduleViewModelAbstract
  ) {}

  ngOnInit(): void {
    this.loadProjects();
    this.setupSubscriptions();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  private setupSubscriptions(): void {
    this.projectViewModel.projects$
      .pipe(takeUntil(this.destroy$))
      .subscribe(projects => {
        this.projects = projects;
        this.buildGanttData();
      });

    this.projectViewModel.loading$
      .pipe(takeUntil(this.destroy$))
      .subscribe(loading => this.loading = loading);

    this.projectViewModel.error$
      .pipe(takeUntil(this.destroy$))
      .subscribe(error => this.error = error);
  }

  loadProjects(): void {
    // TODO: Replace with real userId from auth service
    const userId = 'test-user-id';
    this.projectViewModel.loadProjects(userId);
  }

  private buildGanttData(): void {
    if (!this.projects.length) {
      this.ganttTasks = [];
      this.timelineColumns = [];
      return;
    }

    // Convert projects to gantt tasks
    this.ganttTasks = this.projects.map(project => ({
      id: project.id,
      name: project.name,
      startDate: new Date(project.startDate),
      endDate: new Date(project.endDate),
      progress: this.calculateProgress(project),
      status: project.status,
      schedules: [], // TODO: Load schedules per project
      color: this.getStatusColor(project.status)
    }));

    // Calculate timeline range
    this.calculateTimelineRange();
    this.generateTimelineColumns();
  }

  private calculateTimelineRange(): void {
    if (!this.ganttTasks.length) return;

    const allDates = this.ganttTasks.flatMap(task => [task.startDate, task.endDate]);
    const minDate = new Date(Math.min(...allDates.map(d => d.getTime())));
    const maxDate = new Date(Math.max(...allDates.map(d => d.getTime())));

    // Add padding (1 month before and after)
    this.startDate = new Date(minDate);
    this.startDate.setMonth(this.startDate.getMonth() - 1);
    
    this.endDate = new Date(maxDate);
    this.endDate.setMonth(this.endDate.getMonth() + 1);
  }

  private generateTimelineColumns(): void {
    this.timelineColumns = [];
    const current = new Date(this.startDate);
    
    while (current <= this.endDate) {
      const isToday = this.isSameDay(current, this.today);
      
      switch (this.viewMode) {
        case 'day':
          this.timelineColumns.push({
            date: new Date(current),
            label: this.formatDate(current, 'day'),
            isToday
          });
          current.setDate(current.getDate() + 1);
          break;
          
        case 'week':
          this.timelineColumns.push({
            date: new Date(current),
            label: this.formatDate(current, 'week'),
            isToday
          });
          current.setDate(current.getDate() + 7);
          break;
          
        case 'month':
          this.timelineColumns.push({
            date: new Date(current),
            label: this.formatDate(current, 'month'),
            isToday
          });
          current.setMonth(current.getMonth() + 1);
          break;
      }
    }
  }

  private formatDate(date: Date, mode: 'day' | 'week' | 'month'): string {
    const month = date.getMonth() + 1;
    const day = date.getDate();
    
    switch (mode) {
      case 'day':
        return `${month}/${day}`;
      case 'week':
        return `W${this.getWeekNumber(date)}`;
      case 'month':
        return date.toLocaleDateString('ko-KR', { year: 'numeric', month: 'short' });
      default:
        return '';
    }
  }

  private getWeekNumber(date: Date): number {
    const firstDayOfYear = new Date(date.getFullYear(), 0, 1);
    const pastDaysOfYear = (date.getTime() - firstDayOfYear.getTime()) / 86400000;
    return Math.ceil((pastDaysOfYear + firstDayOfYear.getDay() + 1) / 7);
  }

  private isSameDay(date1: Date, date2: Date): boolean {
    return date1.getFullYear() === date2.getFullYear() &&
           date1.getMonth() === date2.getMonth() &&
           date1.getDate() === date2.getDate();
  }

  private calculateProgress(project: IProject): number {
    // Calculate based on schedules or time elapsed
    const total = project.endDate.getTime() - project.startDate.getTime();
    const elapsed = Date.now() - project.startDate.getTime();
    return Math.min(Math.max((elapsed / total) * 100, 0), 100);
  }

  private getStatusColor(status: ProjectStatusEnum): string {
    switch (status) {
      case ProjectStatusEnum.Receive:
        return '#ff9800';
      case ProjectStatusEnum.Progress:
        return '#2196f3';
      case ProjectStatusEnum.Complete:
        return '#4caf50';
      default:
        return '#9e9e9e';
    }
  }

  // Calculate bar position and width
  getBarStyle(task: GanttTask): any {
    const taskStart = task.startDate.getTime();
    const taskEnd = task.endDate.getTime();
    const timelineStart = this.startDate.getTime();
    const timelineEnd = this.endDate.getTime();
    
    const totalDuration = timelineEnd - timelineStart;
    const taskDuration = taskEnd - taskStart;
    const taskOffset = taskStart - timelineStart;
    
    const left = (taskOffset / totalDuration) * 100;
    const width = (taskDuration / totalDuration) * 100;
    
    return {
      left: `${left}%`,
      width: `${width}%`,
      backgroundColor: task.color,
      height: '80%',
      top: '10%'
    };
  }

  // View mode controls
  changeViewMode(mode: 'day' | 'week' | 'month'): void {
    this.viewMode = mode;
    this.generateTimelineColumns();
  }

  // Zoom controls
  zoomIn(): void {
    if (this.dayWidth < 80) {
      this.dayWidth += 10;
    }
  }

  zoomOut(): void {
    if (this.dayWidth > 20) {
      this.dayWidth -= 10;
    }
  }

  // Navigate timeline
  scrollToToday(): void {
    // TODO: Implement scroll to today
    const todayColumn = this.timelineColumns.find(col => col.isToday);
    if (todayColumn) {
      // Scroll logic here
    }
  }

  // Task interactions
  onTaskClick(task: GanttTask): void {
    // TODO: Open task detail dialog
  }

  formatDateDisplay(date: Date): string {
    return date.toLocaleDateString('ko-KR', { 
      year: 'numeric', 
      month: 'short', 
      day: 'numeric' 
    });
  }

  getStatusText(status: ProjectStatusEnum): string {
    switch (status) {
      case ProjectStatusEnum.Receive:
        return 'ì ‘ìˆ˜';
      case ProjectStatusEnum.Progress:
        return 'ì§„í–‰ì¤‘';
      case ProjectStatusEnum.Complete:
        return 'ì™„ë£Œ';
      default:
        return 'ì•Œ ìˆ˜ ì—†ìŒ';
    }
  }
}
