import { Component, OnInit, inject } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CdkDragDrop, moveItemInArray, transferArrayItem, DragDropModule } from '@angular/cdk/drag-drop';
import { MatCardModule } from '@angular/material/card';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatTooltipModule } from '@angular/material/tooltip';
import { ProjectViewModelAbstract } from '../../abstracts/project-viewmodel.abstract';
import { IProject, ProjectStatusEnum } from '../../interfaces/project.interface';

/**
 * ì¹¸ë°˜ë³´ë“œ ì»´í¬ë„ŒíŠ¸
 */
@Component({
  selector: 'app-kanban',
  standalone: true,
  imports: [
    CommonModule,
    DragDropModule,
    MatCardModule,
    MatButtonModule,
    MatIconModule,
    MatProgressSpinnerModule,
    MatTooltipModule
  ],
  templateUrl: './kanban.html',
  styleUrl: './kanban.scss'
})
export class Kanban implements OnInit {
  projectVM = inject(ProjectViewModelAbstract);
  
  ProjectStatusEnum = ProjectStatusEnum;
  
  // ìƒíƒœë³„ í”„ë¡œì íŠ¸ ëª©ë¡
  receiveProjects: IProject[] = [];
  progressProjects: IProject[] = [];
  completeProjects: IProject[] = [];

  ngOnInit(): void {
    this.projectVM.loadProjects();
    
    // í”„ë¡œì íŠ¸ ëª©ë¡ êµ¬ë…í•˜ì—¬ ìƒíƒœë³„ë¡œ ë¶„ë¥˜
    this.projectVM.projects$.subscribe(projects => {
      this.categorizeProjects(projects);
    });
  }

  /**
   * í”„ë¡œì íŠ¸ë¥¼ ìƒíƒœë³„ë¡œ ë¶„ë¥˜
   */
  private categorizeProjects(projects: IProject[]): void {
    this.receiveProjects = projects.filter(p => p.status === ProjectStatusEnum.Receive);
    this.progressProjects = projects.filter(p => p.status === ProjectStatusEnum.Progress);
    this.completeProjects = projects.filter(p => p.status === ProjectStatusEnum.Complete);
  }

  /**
   * ë“œë˜ê·¸ ì•¤ ë“œë¡­ ì´ë²¤íŠ¸ ì²˜ë¦¬
   */
  drop(event: CdkDragDrop<IProject[]>, newStatus: ProjectStatusEnum): void {
    if (event.previousContainer === event.container) {
      // ê°™ì€ ì»¨í…Œì´ë„ˆ ë‚´ì—ì„œ ì´ë™
      moveItemInArray(event.container.data, event.previousIndex, event.currentIndex);
    } else {
      // ë‹¤ë¥¸ ì»¨í…Œì´ë„ˆë¡œ ì´ë™
      const project = event.previousContainer.data[event.previousIndex];
      
      transferArrayItem(
        event.previousContainer.data,
        event.container.data,
        event.previousIndex,
        event.currentIndex
      );

      // ì„œë²„ì— ìƒíƒœ ë³€ê²½ ìš”ì²­
      this.projectVM.changeProjectStatus(project.id, newStatus);
    }
  }

  /**
   * ìƒˆ í”„ë¡œì íŠ¸ ìƒì„±
   */
  onCreate(): void {
    this.projectVM.openCreateDialog();
  }

  /**
   * í”„ë¡œì íŠ¸ ìˆ˜ì •
   */
  onEdit(project: IProject): void {
    this.projectVM.openEditDialog(project);
  }

  /**
   * í”„ë¡œì íŠ¸ ì‚­ì œ
   */
  onDelete(project: IProject): void {
    this.projectVM.deleteProject(project.id);
  }
}
