import { Injectable, inject } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable, of, catchError } from 'rxjs';
import { map } from 'rxjs/operators';
import { ISchedule, OwnerType, Priority } from '../interfaces/schedule.interface';
import { ProjectStatusEnum } from '../interfaces/project.interface';
import { environment } from '../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ScheduleService {
  private readonly http = inject(HttpClient);
  private readonly baseUrl = environment.baseSyncUrl + '/schedule';

  /**
   * IDë¡œ ìŠ¤ì¼€ì¤„ ì¡°íšŒ
   * SyncBI-Station API: GET /schedule?id={id}
   */
  getScheduleById(id: string): Observable<ISchedule | null> {
    const params = new HttpParams().set('id', id);
    
    return this.http.get<any>(this.baseUrl, { params }).pipe(
      map(schedule => {
        if (!schedule) return null;
        
        // ë‚ ì§œ ì—­ì§ë ¬í™”
        return {
          ...schedule,
          startDate: new Date(schedule.startDate),
          endDate: new Date(schedule.endDate)
        };
      }),
      catchError(error => {
        return of(null);
      })
    );
  }

  /**
   * ë‹´ë‹¹ì IDë¡œ ìŠ¤ì¼€ì¤„ ëª©ë¡ ì¡°íšŒ
   * SyncBI-Station API: GET /schedule?managerId={id}
   */
  getSchedulesByManagerId(managerId: string): Observable<ISchedule[]> {
    const params = new HttpParams().set('managerId', managerId);
    
    return this.http.get<any[]>(this.baseUrl, { params }).pipe(
      map(schedules => {
        
        // ë‚ ì§œ ì—­ì§ë ¬í™”
        return schedules.map(schedule => ({
          ...schedule,
          startDate: new Date(schedule.startDate),
          endDate: new Date(schedule.endDate)
        }));
      }),
      catchError(error => {
        return of([]);
      })
    );
  }

  /**
   * í”„ë¡œì íŠ¸ IDë¡œ ìŠ¤ì¼€ì¤„ ëª©ë¡ ì¡°íšŒ (ownerIdë¡œ í•„í„°ë§)
   */
  getSchedulesByProjectId(projectId: string): Observable<ISchedule[]> {
    // ë°±ì—”ë“œì— í”„ë¡œì íŠ¸ ID ê¸°ë°˜ ì¡°íšŒ APIê°€ ìˆìœ¼ë©´ ì‚¬ìš©
    // í˜„ì¬ëŠ” managerId ê¸°ë°˜ìœ¼ë¡œ ì¡°íšŒ í›„ í•„í„°ë§
    return this.http.get<any[]>(this.baseUrl).pipe(
      map(schedules => schedules
        .filter(s => s.ownerId === projectId && s.ownerType === OwnerType.Project)
        .map(schedule => ({
          ...schedule,
          startDate: new Date(schedule.startDate),
          endDate: new Date(schedule.endDate)
        }))
      ),
      catchError(error => {
        return of([]);
      })
    );
  }

  /**
   * ìƒˆ ìŠ¤ì¼€ì¤„ ìƒì„±
   * SyncBI-Station API: POST /schedule
   */
  createSchedule(payload: {
    ownerId: string;
    ownerType: OwnerType;
    name: string;
    startDate: Date;
    endDate: Date;
    managerId: string;
    description?: string;
    status: ProjectStatusEnum;
    priority: Priority;
  }): Observable<ISchedule> {
    
    return this.http.post<any>(this.baseUrl, payload).pipe(
      map(schedule => {
        
        // ë‚ ì§œ ì—­ì§ë ¬í™”
        return {
          ...schedule,
          startDate: new Date(schedule.startDate),
          endDate: new Date(schedule.endDate)
        };
      }),
      catchError(error => {
        throw error;
      })
    );
  }

  /**
   * ìŠ¤ì¼€ì¤„ ì—…ë°ì´íŠ¸
   * SyncBI-Station API: PATCH /schedule
   */
  updateSchedule(payload: Partial<ISchedule> & { id: string }): Observable<ISchedule> {
    
    return this.http.patch<any>(this.baseUrl, payload).pipe(
      map(schedule => {
        
        // ë‚ ì§œ ì—­ì§ë ¬í™”
        return {
          ...schedule,
          startDate: new Date(schedule.startDate),
          endDate: new Date(schedule.endDate)
        };
      }),
      catchError(error => {
        throw error;
      })
    );
  }

  /**
   * ìŠ¤ì¼€ì¤„ ìƒíƒœë§Œ ì—…ë°ì´íŠ¸ (ì¹¸ë°˜ë³´ë“œ Drag & Dropìš©)
   */
  updateScheduleStatus(id: string, status: ProjectStatusEnum): Observable<ISchedule> {
    return this.updateSchedule({ id, status });
  }

  /**
   * ìŠ¤ì¼€ì¤„ ì‚­ì œ
   * SyncBI-Station API: DELETE /schedule?id={id}
   */
  deleteSchedule(id: string): Observable<void> {
    const params = new HttpParams().set('id', id);
    
    return this.http.delete<void>(this.baseUrl, { params }).pipe(
      map(() => {
      }),
      catchError(error => {
        throw error;
      })
    );
  }
}
