import { Injectable, inject } from '@angular/core';
import { BehaviorSubject, Subject, Observable } from 'rxjs';
import { MatDialog } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { IProject, ProjectStatusEnum } from '../interfaces/project.interface';
import { ProjectService } from '../services/project.service';

/**
 * í”„ë¡œì íŠ¸ ViewModel ì¶”ìƒ í´ë˜ìŠ¤
 * MVVM íŒ¨í„´ì˜ ViewModel ì—­í• 
 */
@Injectable()
export abstract class ProjectViewModelAbstract {
  // ì˜ì¡´ì„± ì£¼ì…
  protected projectService = inject(ProjectService);
  protected dialog = inject(MatDialog);

  // ìƒíƒœ ê´€ë¦¬ (RxJS BehaviorSubject)
  projects$ = new BehaviorSubject<IProject[]>([]);
  selectedProject$ = new BehaviorSubject<IProject | null>(null);
  loading$ = new BehaviorSubject<boolean>(false);
  error$ = new BehaviorSubject<string | null>(null);

  // í…Œì´ë¸” ë°ì´í„°
  dataSource = new MatTableDataSource<IProject>([]);
  displayedColumns: string[] = ['name', 'status', 'startDate', 'endDate', 'description', 'actions'];

  // Destroy subject for cleanup
  protected destroy$ = new Subject<void>();

  constructor() {
    this.init();
  }

  /**
   * ì´ˆê¸°í™”
   */
  protected init(): void {
    // í”„ë¡œì íŠ¸ ëª©ë¡ êµ¬ë…í•˜ì—¬ í…Œì´ë¸” ë°ì´í„° ì—…ë°ì´íŠ¸
    this.projects$.subscribe(projects => {
      this.dataSource.data = projects;
    });
  }

  /**
   * ì„œë²„ì—ì„œ í”„ë¡œì íŠ¸ ëª©ë¡ ë¡œë“œ
   */
  abstract loadProjects(organizationId?: string): void;

  /**
   * í”„ë¡œì íŠ¸ ì„ íƒ
   */
  selectProject(project: IProject): void {
    this.selectedProject$.next(project);
  }

  /**
   * í”„ë¡œì íŠ¸ ìƒì„± ë‹¤ì´ì–¼ë¡œê·¸ ì—´ê¸°
   */
  abstract openCreateDialog(): void;

  /**
   * í”„ë¡œì íŠ¸ ìˆ˜ì • ë‹¤ì´ì–¼ë¡œê·¸ ì—´ê¸°
   */
  abstract openEditDialog(project: IProject): void;

  /**
   * í”„ë¡œì íŠ¸ ì‚­ì œ
   */
  abstract deleteProject(projectId: string): void;

  /**
   * í”„ë¡œì íŠ¸ ìƒíƒœ ë³€ê²½
   */
  abstract changeProjectStatus(projectId: string, status: ProjectStatusEnum): void;

  /**
   * ì—ëŸ¬ ì²˜ë¦¬
   */
  protected handleError(error: any): void {
    this.error$.next(error.message || 'An error occurred');
    this.loading$.next(false);
  }

  /**
   * ì •ë¦¬
   */
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
